import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import Button from "./elemenets/Button/Button";
import Loader from "./elemenets/Loader/Loader";

import { motion } from "framer-motion";

export default function UI(props) {

	useEffect(() => {
		props.setActiveSection("ui");
	  }, [props.activeSection]);

  return (
    <motion.div
		variants={props.variants}
		initial="hidden"
		animate="visible"
		exit="exit"
	>
      <h1> 1</h1>
      <h2> 2</h2>
      <h3> 3</h3>
      <h4> 4</h4>
      <h5> 5</h5>
      <h6> 6</h6>
      <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas neque
        enim labore totam, numquam reprehenderit harum non hic quod. Provident
        voluptatem veniam voluptate quia asperiores commodi recusandae,
        voluptates doloremque praesentium.
      </p>
      <p>
        -         
         . ,       
          .   ,   
          ?
      </p>
      <h2>Buttons</h2>
      <Button buttonValue=" SM" />
      <br />
      <Button buttonValue=" MD" buttonModifier="btn-md" />
      <br />
      <Button buttonValue=" LG" buttonModifier="btn-lg" />
      <br />
      <Button
        buttonValue=" DISABLED"
        buttonModifier="btn-md"
        isButtonDisabled={true}
      />
      <br />
      <Button buttonValue=" SUCCESS" buttonModifier="btn-success" />
      <br />
      <h2>Link</h2>
      <a href="https://youtube.com"></a>
      <Link to="/">Main</Link>
      <h2>Unordered list</h2>
      <ul>
        <li> Lorem, ipsum dolor. </li>
        <li>Lorem ipsum dolor sit amet.</li>
        <li>Lorem, ipsum dolor.</li>
        <li>-     .</li>
        <li>Lorem ipsum dolor sit.</li>
        <li>Lorem, ipsum dolor.</li>
      </ul>
      <h2>Ordered list</h2>
      <ol>
        <li> Lorem, ipsum dolor. </li>
        <li>Lorem ipsum dolor sit amet.</li>
        <li>Lorem, ipsum dolor.</li>
        <li>-     .</li>
        <li>Lorem ipsum dolor sit.</li>
        <li>Lorem, ipsum dolor.</li>
      </ol>
      <Loader />
    </motion.div>
  );
}
